import { z } from 'zod';

export const AddMaintenanceCenterSchema = z.object({
    name: z.string().min(1, 'Maintenance center name is required'),
    status: z.enum(['active', 'not active'], {
        required_error: 'Status is required',
    }),
    location: z.enum(['الرياض', 'الشرقية'], {
        required_error: 'Transaction status is required',
    }),
    contact_number: z
        .string()
        .min(1, 'رقم الهاتف مطلوب')
        .refine((val) => /^\d+$/.test(val), 'الرجاء إدخال أرقام فقط')
        .refine((val) => val.length >= 8 && val.length <= 15, 'يجب أن يتكون رقم الهاتف من 8 إلى 15 رقم'),
    description: z.string().min(1, 'Description is required'),
    contract_status: z.enum(['active', 'not active'], {
        required_error: 'Contract status is required',
    }),
    features: z
        .array(z.string())
        .optional()
        .default([])
        .transform((val) => val || []),
});

export type AddMaintenanceCenterFormValues = z.infer<typeof AddMaintenanceCenterSchema>;
